import React,{Component} from 'react';
import Layout from './components/Layout';
// import bg5 from './images/bg5.png';
import okButton from './images/okButton.png';
import Modal from 'react-modal';
import pa2 from './images/pa2.jpg';
Modal.setAppElement('#root')
const medalStyles = {
    content : {
      top                   : '50%',
      left                  : '50%',
      right                 : 'auto',
      bottom                : 'auto',
      marginRight           : '-50%',
      transform             : 'translate(-50%, -50%)'
    }
  };
const redColor = "#f92a36"
const blueColor = "#293bf9"
const blackColor = "#1f2126"
class Page2 extends Component {
    constructor() {
        super();
    
        this.state = {
          colorModalIsOpen: false,
          thicknessModalIsOpen: false,
          mode : "draw",
          thickness : "",
          color : blackColor ,
          thicknessValue : "5"
        };
    
    }
    openColorModal = () => {
        this.setState({colorModalIsOpen: true});
    }
    openThicknessModal = () =>{
        this.setState({thicknessModalIsOpen: true});
    }
    
    afterOpenModal = () => {
        // references are now sync'd and can be accessed.
        // this.subtitle.style.color = '#f00';
    }
    clearButtonClick = () => {
        this.layout.handleClear()
    }
    modeButtonClick = () => {
        this.setState((prevState, props) => {
            var mode = "draw"
            if (prevState.mode === "draw"){
                mode = "erase"
                this.layout.handleThickness(30)
            }else{
                mode = "draw"
                this.changeThickness(prevState.thicknessValue)
            }
            this.layout.handleMode(mode)
            return {
              mode: mode
            };
          });
    }
    changeColor = (color) => {
        console.log(color)
        this.layout.handleColorChange(color)
        this.setState({colorModalIsOpen: false , color: color});
    }
    changeThickness(thickness){
        this.layout.handleThickness(thickness)
        var thinkes = ""
        if (thickness === "5"){

        }else if (thickness === "7"){
            thinkes = ""
        }else {
            thinkes = ""
        }
        this.setState({thicknessModalIsOpen:false , thickness : thinkes , thicknessValue: thickness});
    }
    closeModal = () => {
        this.setState({colorModalIsOpen: false});
    }
    okbuttonClick = () => {
        this.props.onOkButtonClick()
    }
    render(){
        var buttonStyle = {orderWidth: "0.5px", 
        backgroundColor: "rgba(ee, 0, 0, 0.5)", 
        height : "200px",
        width: "200px",
        fontSize: "50px",
         }

        let colorButtonStyle = {
            borderStyle: "solid",
            borderWidth: "0.5px", 
            height : "200px",
            width: "200px",
        }
        let redButtonStyle = {backgroundColor: redColor}
        let blueButtonStyle = {backgroundColor: blueColor}
        let blackButtonStyle = {backgroundColor: blackColor}
        return (
        <div className="bg" >
        <div className="bg" style={{backgroundImage:`url(${pa2})` ,
            opacity: "1",
            backgroundRepeat: "no-repeat",
            backgroundAttachment: "fixed",
            backgroundPosition: "center"
        }}></div>
        
            <Modal
                isOpen={this.state.colorModalIsOpen}
                onAfterOpen={this.afterOpenModal}
                onRequestClose={this.closeModal}
                style={medalStyles}
                shouldCloseOnOverlayClick={false}
                contentLabel="Color"
            >
                <button style={{...colorButtonStyle, ...redButtonStyle}} onClick={() => this.changeColor(redColor)}></button>
                <button style={{...colorButtonStyle, ...blueButtonStyle}} onClick={() => this.changeColor(blueColor)}></button>
                <button style={{...colorButtonStyle, ...blackButtonStyle}} onClick={()=> this.changeColor(blackColor)}></button>
            </Modal>
            <Modal
                isOpen={this.state.thicknessModalIsOpen}
                onAfterOpen={this.afterOpenModal}
                onRequestClose={this.closeModal}
                style={medalStyles}
                shouldCloseOnOverlayClick={false}
                contentLabel="thickness"
            >
                <button style={buttonStyle} onClick={this.changeThickness.bind(this,"5")}></button>
                <button style={buttonStyle} onClick={this.changeThickness.bind(this,"7")}></button>
                <button style={buttonStyle} onClick={this.changeThickness.bind(this,"10")}></button>
            </Modal>
        <Layout ref={ref => this.layout = ref}/>
        {/* <button className="page2clearButton" onClick={this.clearButtonClick}>clear</button> */}
        <button className="page2clearButton" style={{...buttonStyle , ...{opacity: "0"}}} onClick={this.modeButtonClick}>{this.state.mode}</button>
        {/* <button className="changeColorButton" style={{...buttonStyle , ...{backgroundColor : this.state.color}}} onClick={this.openColorModal}></button> */}
        <button className="changeThicknessButton" style={{...buttonStyle , ...{opacity: "0"}}} onClick={this.openThicknessModal}>{this.state.thickness}</button>
        <button className="startButton">
        <img className= "startButtonImage" src={okButton} alt="" onClick={this.okbuttonClick} />
        </button>
        
        </div>)
    }
}
export default Page2;


